import { Box, IconButton, useTheme } from "@mui/material";
import { useContext } from "react";
import { ColorModeContext, tokens } from "../../theme";
import {InputBase} from "@mui/material";
import LightModeOutlinedIcon from '@mui/icons-material/LightModeOutlined';
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';
import SearchIcon from "@mui/icons-material/Search";
import MenuOutlinedIcon from '@mui/icons-material/MenuOutlined';
import LanguageOutlinedIcon from '@mui/icons-material/LanguageOutlined';
import { useThemeShadcn } from "../../components/ui/theme-provider";

const Topbar = ({
    toggled,
    handleToggleSidebar,
    broken
}) => {
    
    const theme = useTheme();
    const colors = tokens(theme.palette.mode);
    const colorMode = useContext(ColorModeContext);
    const { setTheme } = useThemeShadcn();
    

    const handleThemeChange = () => {
        colorMode.toggleColorMode();
        
        // actualizamos el color para la libreria shadch-ui
        setTheme(theme.palette.mode === 'light' ? 'dark': 'ligth');
    }

    return (
        <Box display="flex" justifyContent="space-between" p={2}>
            {/**sidebar Toolbar */}
            {
                broken && (
                    <Box display="flex">
                        <IconButton onClick={() => handleToggleSidebar(!toggled)}>
                            <MenuOutlinedIcon />
                        </IconButton>
                    </Box>
                )
            }
            {/** SEARCH BAR */}
            <Box display="flex" backgroundColor={colors.primary[400]} borderRadius="3px">
                <InputBase sx={{ ml: 2, flex: 1 }} placeholder="Search" />
                <IconButton type="button" sx={{p: 1}}>
                    <SearchIcon/>
                </IconButton>
            </Box>
            
            {/**ICONS */}
            <Box display="flex">
                <IconButton onClick={handleThemeChange}>
                    {theme.palette.mode === 'dark' ? (
                        <DarkModeOutlinedIcon />
                    ) : <LightModeOutlinedIcon />
                    }
                </IconButton>
                <IconButton>
                    <LanguageOutlinedIcon />
                </IconButton>
                <IconButton>
                    <PersonOutlineOutlinedIcon />
                </IconButton>
            </Box>
        </Box>
    )
}

export default Topbar;